import React from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Offcanvas from 'react-bootstrap/Offcanvas';

const OffcanvasExample = ({ getScores, showTheNewq, reset, start, startOver, isQuizPage }) => {
    return (
        <>
            {['xxl'].map((expand) => (
                <Navbar key={expand} style={{ background: '#9EBCC6', paddingLeft: '40px', paddingRight: '40px', }} expand={expand}>
                    <Container fluid>
                        <Navbar.Brand href="#" className='fw-bold'> Malawi Simple Quiz </Navbar.Brand>
                        <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-${expand}`} />
                        <Navbar.Offcanvas
                            id={`offcanvasNavbar-expand-${expand}`}
                            aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`}
                            placement="end"
                        >
                            <Offcanvas.Header closeButton>
                                <Offcanvas.Title id={`offcanvasNavbarLabel-expand-${expand}`}>
                                    Malawi Quiz
                                </Offcanvas.Title>
                            </Offcanvas.Header>
                            <Offcanvas.Body>
                                <Nav className="justify-content-end flex-grow-1 pr-2 pe-1">
                                    <Nav.Link
                                        href="#action1"
                                        style={{ paddingLeft: '15px', paddingRight: '15px', marginLeft: '15px', marginRight: '15px' }}
                                        className='fw-bold border rounded'
                                        onClick={() => { reset(); start(); startOver(); }}>
                                        {(isQuizPage !== 'True')
                                            ? <div>Take Quiz</div>
                                            : <div>Restart Quiz</div>}
                                    </Nav.Link>
                                    {/* {(isQuizPage !== 'True')
                                        ? 
                                        <Nav.Link
                                            href="#action1"
                                            style={{ paddingLeft: '15px', paddingRight: '15px', marginLeft: '15px', marginRight: '15px' }}
                                            className='fw-bold border rounded'
                                            onClick={() => { showTheNewq(); getScores(); }}> Leader Board 
                                        </Nav.Link>
                                        : 
                                        <Nav.Link
                                            href="#action1"
                                            style={{ paddingLeft: '15px', paddingRight: '15px', marginLeft: '15px', marginRight: '15px' }}
                                            variant="outline-success"
                                            className='text-muted fw-bold disabled border rounded'
                                            onClick={() => { showTheNewq(); getScores(); }}> Leader Board 
                                            
                                        </Nav.Link>
                                    } */}

                                    <Nav.Link
                                        href="#action2"
                                        style={{ paddingLeft: '15px', paddingRight: '15px', marginLeft: '15px', marginRight: '15px' }}
                                        variant="outline-success"
                                        className='text-muted fw-bold disabled border rounded'>
                                        Create Quiz
                                    </Nav.Link>
                                </Nav>
                            </Offcanvas.Body>
                        </Navbar.Offcanvas>
                    </Container>
                </Navbar>
            ))}
        </>
    );
}

export default OffcanvasExample;