import React, { useEffect, useState } from 'react';
import Start from './components/Start';
import Quiz from './components/Quiz';
import $ from "jquery";
import Result from './components/Result';
import Createnew from "./components/Createnew";
import { useTimer } from 'use-timer';
import axios from 'axios';

function App() {
  // All Quizs, Current Question, Index of Current Question, Answer, Selected Answer, Total Marks
  const [quizs, setQuizs] = useState([]);
  const [question, setQuesion] = useState({});
  const [questionIndex, setQuestionIndex] = useState(0);
  const [correctAnswer, setCorrectAnswer] = useState('');
  const [selectedAnswer, setSelectedAnswer] = useState('');
  const [marks, setMarks] = useState(0);
  const [inputs, setInputs] = useState({});
  const { start, reset, pause, time } = useTimer();
  const [scores, setScores] = useState([]);

  // Display Controlling States
  const [showStart, setShowStart] = useState(true);
  const [showQuiz, setShowQuiz] = useState(false);
  const [showResult, setShowResult] = useState(false);
  const [showCreatenew, setShowCreatenew] = useState(false);

  // Load JSON Data
  useEffect(() => {
    const shuffle = (array) => {
      for (var i = array.length - 1; i > 0; i--) {
        var j = Math.floor(Math.random() * (i + 1));
        var temp = array[i];
        array[i] = array[j];
        array[j] = temp;
      }
    };

    fetch(`${process.env.REACT_APP_DATA}`)

      .then(res => res.json())
      .then(data => {
        const currentcat = inputs.category;
        shuffle(data);
        setQuizs(data.filter(cats => cats.category == currentcat).slice(0, 10))
      })
  }, [inputs]);

  useEffect(() => {
    getScores();
  }, []);

  // Set a Single Question
  useEffect(() => {
    if (quizs.length > questionIndex) {
      setQuesion(quizs[questionIndex]);
    }
  }, [quizs, questionIndex])

  // Start Quiz
  const startQuiz = () => {
    setShowStart(false);
    setShowQuiz(true);
    setShowCreatenew(false);
  }

  // Create Quiz
  const showTheNewq = () => {
    setShowCreatenew(true);
    setShowResult(false);
    setShowStart(false);
    setShowQuiz(false);
  }

  // Check Answer
  const checkAnswer = (event, selected) => {
    if (!selectedAnswer) {
      setCorrectAnswer(question.answer);
      setSelectedAnswer(selected);

      if (selected === question.answer) {
        event.target.classList.add('bg-success');
        setMarks(marks + 1);
      } else {
        event.target.classList.add('bg-danger');
      }
    }
  }

  // Next Quesion
  const nextQuestion = () => {
    setCorrectAnswer('');
    setSelectedAnswer('');
    const wrongBtn = document.querySelector('button.bg-danger');
    wrongBtn?.classList.remove('bg-danger');
    const rightBtn = document.querySelector('button.bg-success');
    rightBtn?.classList.remove('bg-success');
    setQuestionIndex(questionIndex + 1);
  }

  // Show Result
  const showTheResult = () => {
    setShowResult(true);
    setShowStart(false);
    setShowQuiz(false);
  }

  // Start Over
  const startOver = () => {
    setShowStart(true);
    setShowResult(false);
    setShowQuiz(false);
    setShowCreatenew(false);
    setCorrectAnswer('');
    setSelectedAnswer('');
    setQuestionIndex(0);
    setMarks(0);
    const wrongBtn = document.querySelector('button.bg-danger');
    wrongBtn?.classList.remove('bg-danger');
    const rightBtn = document.querySelector('button.bg-success');
    rightBtn?.classList.remove('bg-success');
  }

  //FORM
  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;

    setInputs(values => ({ ...values, [name]: value }))
  };

  const handleSumbit = (e) => {
    e.preventDefault();
  };

  const SaveResults = () => {

    const values = [{
      'name': inputs.name,
      'category': (inputs.category),
      'time_taken': time,
      'score': (Math.round((marks / quizs.length) * 100)),
      'correct_ans': marks,
      'wrong_ans': (quizs.length - marks),
      'total_q': (quizs.length)
    }]

    $.ajax({
      type: "POST",
      url: `${process.env.REACT_APP_RESULTS}`,
      data: { values: JSON.stringify(values) },
    });
  }

  function getScores() {
      axios.get(`${process.env.REACT_APP_SCORES}`).then(function (response) {
      setScores(response.data);
    });
  }

  return (
    <>
      {/* Welcome Page */}
      <Start
        startQuiz={startQuiz}
        showStart={showStart}
        handleChange={handleChange}
        handleSumbit={handleSumbit}
        name={inputs.name}
        category={inputs.category}
        start={start}
        showTheNewq={showTheNewq}
        getScores={getScores}
        reset={reset}
      />

      {/* Quiz Page */}
      <Quiz
        category={inputs.category}
        name={inputs.name}
        showQuiz={showQuiz}
        question={question}
        quizs={quizs}
        checkAnswer={checkAnswer}
        correctAnswer={correctAnswer}
        selectedAnswer={selectedAnswer}
        questionIndex={questionIndex}
        nextQuestion={nextQuestion}
        showTheResult={showTheResult}
        start={start}
        startOver={startOver}
        reset={reset}
        time={time}
        pause={pause}
        SaveResults={SaveResults}
        getScores={getScores}
        showTheNewq={showTheNewq}
      />

      {/* Result Page */}
      <Result
        showResult={showResult}
        name={inputs.name || ""}
        quizs={quizs}
        marks={marks}
        startOver={startOver}
        showTheNewq={showTheNewq}
        reset={reset}
        start={start}
        getScores={getScores}
        time={time}
      />

      <Createnew
        showCreatenew={showCreatenew}
        scores={scores}
        getScores={getScores}
        showTheNewq={showTheNewq}
        reset={reset}
        start={start}
        startOver={startOver}
      />
    </>
  );
}

export default App;
