import Logo from '../logo.png'

const Header = () => {
    return (
        <div className='row align-items-center justify-content-center text-center'>
            <div className="">
                <img className="mt-2 pt-2 img-fluid" src={Logo} alt="Logo" />
                <h2 className='fw-bold pt-2 mt-2' style={{ color: '#9EBCC6' }}> Malawi Simple Quiz </h2>
            </div>
        </div>
    )
}

export default Header;