import React from 'react';
import Header from './Header.js';
import Footer from './Footer.js';
import Nav from './nav';
// import { CircularProgressbar } from 'react-circular-progressbar';

const Quiz = ({ pause, time, start, reset, startOver, showTheNewq, getScores, SaveResults, showQuiz, question, quizs, checkAnswer, correctAnswer, selectedAnswer, questionIndex, nextQuestion, showTheResult }) => {

    return (
        <section className="bg-white text-dark" style={{ display: `${showQuiz ? 'block' : 'none'}` }}>
            <div className="container-fluid d-flex flex-column min-vh-100">
                <div className='row sticky-top'>
                    <Nav
                        showTheNewq={showTheNewq}
                        getScores={getScores}
                        reset={reset}
                        start={start}
                        startOver={startOver}
                        isQuizPage='True'
                    />
                </div>
                <div className='row align-items-center justify-content-center text-center d-flex flex-column col-xs-12 col-md-12 p--5'>
                    <Header />
                </div>
                <div className="row align-items-center justify-content-center text-center d-flex flex-column p-5">
                    <div className="col-xs-12 col-md-8 col-sm-12 col-">
                        <div className="card p-4" style={{ background: '#9EBCC6', borderColor: '#EDECEB' }}>
                            <div className="d-flex justify-content-between gap-md-3">
                                <h5 className='mb-2 fs-normal lh-base'>{question?.question}</h5>
                            </div>
                            <div>
                                {
                                    question?.options?.map((item, index) => <button
                                        key={index}
                                        className={`option w-100 text-start btn text-dark py-2 px-3 mt-3 rounded ${correctAnswer === item && 'bg-success'}`}
                                        onClick={(event) => checkAnswer(event, item)}
                                        style={{ background: '#D8E4E8' }}
                                    >
                                        {item}
                                    </button>)
                                }
                            </div>

                            {
                                (questionIndex + 1) !== quizs.length ?
                                    <button className='btn py-2 w-100 mt-3 bg-dark text-light fw-bold' onClick={nextQuestion} disabled={!selectedAnswer}>Next Question</button>
                                    :
                                    <button className='btn py-2 w-100 mt-3 bg-dark text-light fw-bold' onClick={() => { showTheResult(); SaveResults(); pause(); }} disabled={!selectedAnswer}>Show Result</button>
                            }
                        </div>
                        <div class="text-end ml-4 pl-4">
                            <i id='' className='p-1 pl-4 ml-4'> Timer: {Math.floor(time / 60)} :</i>
                            <i className='p-1'> {time % 60} </i>
                        </div>
                    </div>

                </div>
                <div className='row mt-auto' style={{ background: '#9EBCC6', borderColor: '#EDECEB' }}>
                    <Footer />
                </div>
            </div>
        </section>
    );
};

export default Quiz;