import React from 'react';
import { FaPhoneSquareAlt } from "@react-icons/all-files/fa/FaPhoneSquareAlt";
import { MdEmail } from "@react-icons/all-files/md/MdEmail";

const Footer = () => {
    return (
        <footer className="row text-center text-lg-start">
            <section className="pb-3 pt-3" style={{ background: "" }} >
                <div className='row'>
                    <div className='col-md-4 text-center'>
                        © 2023 Copyright: Malawi Simple Quiz
                    </div>
                    <div className='col-md-4 text-center'>
                        < MdEmail /> info@malawianinfo.com
                    </div>
                    <div className='col-md-4 text-center'>
                        < FaPhoneSquareAlt /> + 265 888 394 136 / + 265 999 394 136
                    </div>
                </div>
            </section>
        </footer>
    )
}

export default Footer;