import React from 'react';
import Header from './Header.js';
import Footer from './Footer.js';
import Table from 'react-bootstrap/Table';
import Nav from './nav'

const Createnew = ({ showTheNewq, getScores, scores, showCreatenew, reset, start, startOver }) => {
    return (
        <section className=' card text-dark bg-white' style={{ display: `${showCreatenew ? 'block' : 'none'}` }}>
            <div className="container-fluid d-flex flex-column min-vh-100">
                <div className='row sticky-top'>
                    <Nav
                        showTheNewq={showTheNewq}
                        getScores={getScores}
                        reset={reset}
                        start={start}
                        startOver={startOver}
                    />
                </div>
                <div className="row justify-content-center">
                    <Header />
                    <div className="col-lg-8 pt-2 mt-2">
                        <h4 className='text-center mb-2 pb-2' style={{ color: '#9EBCC6' }}> Leader Board </h4>
                        <Table className="movies" striped bordered hover size="sm" >
                            <thead style={{ background: '#9EBCC6' }}>
                                <tr>
                                    <th>Name</th>
                                    <th>Time Taken</th>
                                    <th>Score</th>
                                </tr>
                            </thead>
                            <tbody>
                                {scores.map((score, key) =>

                                    <tr key={key} style={{ align: 'left' }}>
                                        <td>{score.name}</td>
                                        <td>{Math.floor(score.time_taken / 60)}:{Math.floor(score.time_taken % 60)}</td>
                                        <td>{score.score}</td>
                                    </tr>
                                )}
                            </tbody>
                        </Table>
                    </div>
                </div>
                <div className='row mt-auto' style={{ background: '#9EBCC6', borderColor: '#EDECEB' }}>
                    <Footer />
                </div>
            </div>
        </section>
    );
};

export default Createnew;