import React from 'react';
import Header from './Header';
import Footer from './Footer';
import Nav from './nav'
import './custom.css';
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';

const Result = ({ time, getScores, reset, start, showTheNewq, showResult, name, quizs, marks, startOver }) => {
    return (
        <section className="bg-white text-dark" style={{ display: `${showResult ? 'block' : 'none'}` }}>
            <div className="container-fluid d-flex flex-column min-vh-100">
                <div className='row sticky-top'>
                    <Nav
                        showTheNewq={showTheNewq}
                        getScores={getScores}
                        reset={reset}
                        start={start}
                        startOver={startOver}
                    />
                </div>
                <div className="row align-items-center text-center justify-content-center">
                    <Header />
                    <div className="col-lg-6 pt-5 pb-5">
                        <div className={`text-light text-center p-5 rounded ${((marks / quizs.length) * 100) > 20 ? 'bg-success' : 'bg-danger'}`}>
                            <h1 className='mb-2 fw-bold'>{((marks / quizs.length) * 100) > 20 ? 'Awesome!' : 'Oops!'}</h1>
                            <h3 className='mb-3 fw-bold'> {name === "" ? "" : name.split(" ", 1)} your score is {marks} out of {quizs.length}</h3>
                            <div
                                className='d-inline-flex'
                                style={{ width: 100, height: 100 }}
                            >
                                <CircularProgressbar
                                    value={Math.round((marks / quizs.length) * 100)}
                                    text={`${Math.round((marks / quizs.length) * 100)}%`}
                                    strokeWidth={15}
                                    styles={buildStyles({
                                        textColor: "white",
                                        pathColor: "white",
                                        trailColor: "red"
                                    })}
                                />

                            </div>
                            <h5 class="text-center pt-2 pb-2">
                                <i id='' className='p-1'> Time Spent: {Math.floor(time / 60)} :</i>
                                <i className='p-1'> {time % 60} </i>
                            </h5>
                            <div className='row pt-2' >
                                <div className='col-md-6 pt-1 mt-1'>
                                    <button onClick={() => { reset(); start(); startOver(); }} className='d-block-flex btn py-2 px-4 btn-light fw-bold d-inline'>Start Over</button>
                                </div>
                                {/* <div className='col-md-6 pt-1 mt-1'>
                                    <button onClick={() => { showTheNewq(); getScores(); }} className="d-block-flex btn py-2 px-4 bg-primary text-white fw-bold" type="submit">
                                        Leader Board </button>
                                </div> */}
                            </div>
                        </div>
                    </div>
                </div>
                <div className='row mt-auto' style={{ background: '#9EBCC6', borderColor: '#EDECEB' }}>
                    <Footer />
                </div>
            </div>
        </section>
    );
};

export default Result;