import React from 'react';
import Header from './Header.js';
import Footer from './Footer.js';
import Nav from './nav'

const Start = ({ showTheNewq, getScores, cates, reset, start, startQuiz, showStart, handleSumbit, handleChange, name, category }) => {

    return (
        <section className='card text-dark text-center' style={{ background: '#f9f9f9', display: `${showStart ? 'block' : 'none'}` }}>
            <div className="container-fluid d-flex flex-column min-vh-100" style={{ background: '#fff' }}>
                <div className='row sticky-top'>
                    <Nav
                        showTheNewq={showTheNewq}
                        getScores={getScores}
                    />
                </div>
                <div className="row justify-content-center">
                    <Header /> {cates}
                    <div className="col-lg-8 pt-4 mt-4">
                        <form
                            onSubmit={(event) => handleSumbit(event)}
                        >
                            <div className='row pb-4'>
                                <div className='col-md-3'></div>
                                <div className='col-md-6'>
                                    <input
                                        className="form-control col-xs-2"
                                        placeholder="Enter your name"
                                        type="text"
                                        id="name"
                                        name="name"
                                        value={name || ""}
                                        onChange={(event) => handleChange(event)}
                                    />
                                </div>
                                <div className='col-md-3'></div>

                            </div>
                            <div className='row pb-4'>
                                <div className='col-md-3'></div>
                                <div className='col-md-6'>
                                    <select

                                        className="form-control col-xs-2"
                                        placeholder="Choose Quiz Category"
                                        type="text"
                                        disabled={name ? false : true}
                                        id="category"
                                        name="category"
                                        value={category || ""}
                                        onChange={(event) => handleChange(event)}>
                                        <option value=""> Choose Category </option>
                                        <option value="general"> General </option>
                                        <option value="history"> History</option>
                                        <option value="sports"> Sports</option>
                                        <option value="entertainment"> Entertainment </option>
                                        <option value="traditional"> Traditional </option>
                                    </select>
                                </div>
                                <div className='col-md-3'></div>

                            </div>
                            <button
                                disabled={category ? false : true}
                                onClick={() => { reset(); start(); startQuiz(); }}
                                className="btn  mt-4 px-4 py-2 text-dark fw-bold"
                                style={{ background: '#9EBCC6' }}
                                type="submit">Start Quiz</button>
                        </form>
                    </div>
                </div>
                <div className='row mt-auto' style={{ background: '#9EBCC6', borderColor: '#EDECEB' }}>
                    <Footer />
                </div>
            </div>
        </section>
    );
};

export default Start;